import WebTorrent from 'webtorrent'
import {useState, useEffect} from 'react';

const client = new WebTorrent()

function App() {
  const [process, setProcess] = useState(0)
  const [vid, setVid] = useState()
  const [name , setName] = useState()
  const magnet = window.location.search

  useEffect(() => {
    client.add(`magnet:?xt=urn:btih:4050ab795f013c8032fbd5056609a9df4d5dd446&dn=Monster+Hunter+(2020)+%5B720p%5D+%5BBluRay%5D+%5BYTS.MX%5D&tr=udp%3A%2F%2Ftracker.opentrackr.org%3A1337%2Fannounce&tr=udp%3A%2F%2Ftracker.leechers-paradise.org%3A6969%2Fannounce&tr=udp%3A%2F%2F9.rarbg.to%3A2710%2Fannounce&tr=udp%3A%2F%2Fp4p.arenabg.ch%3A1337%2Fannounce&tr=udp%3A%2F%2Ftracker.cyberia.is%3A6969%2Fannounce&tr=http%3A%2F%2Fp4p.arenabg.com%3A1337%2Fannounce&tr=udp%3A%2F%2Ftracker.internetwarriors.net%3A1337%2Fannounce&tr=wss%3A%2F%2Ftracker.btorrent.xyz&tr=wss%3A%2F%2Ftracker.openwebtorrent.com` ,(torrent) => {
        const file = torrent.files.find(file => file.name.endsWith('mp4'))
        file.appendTo('.video')
        setName(file?.name)
        setInterval(() => setProcess(file.progress), 300)
    })
  }, [])

  client.on('error', console.log)
  return (
    <div className="App">
      <div className="video" >
      </div>
      Progress: {`${process?.toFixed(2)} %`}<br />
      Name: {name}<br />
    </div>
  );
}

export default App;
